
import Vue from 'vue'
import Tokenizer, { Constructor } from '@/tokenizer'
import testapikey from '@/docs/helpers/testapikey.ts'

export default Vue.extend({
  name: 'Sections',
  data() {
    return {
      exampleFull: (null as any),
      exampleFullResponse: ''
    }
  },
  mounted() {
    this.exampleFull = new Tokenizer({
      apikey: testapikey,
      container: document.querySelector('#exampleFull') as HTMLDivElement,
      submission: (resp: any) => {
        this.exampleFullResponse = resp
      },
      settings: {
        user: {
          showInline: true,
          showName: true,
          showEmail: true,
          showPhone: true,
          showTitle: true
        },
        shipping: {
          show: true,
          showTitle: true
        },
        billing: {
          show: true,
          showTitle: true
        },
        payment: {
          showTitle: true
        }
      }
    })
  }
})
